import React, {Component} from 'react';
import './Styles/App.css';
import Mainblock from './Components/Mainblock';
import Hourly from './Components/Hourly';
import Weekly from './Components/Weekly';

class App extends Component {
  constructor() {
    super() 

    this.state = {
      weatherData: [],
      userIp: 0,
      cityName: '',
      cityNameNoSpace: '',
      userLat: 0,
      userLng: 0,
      currentDt: 0,
      currentTime: undefined,
      country: '',
      width: window.innerWidth
    }

  }
  

  componentDidMount(){

    fetch("https://geoip-db.com/json/").then((data)=> {

      return data.json();
    }).then((ip)=>{

      this.setState({userIp: ip.IPv4});
      this.locateClient(this.state.userIp);
    });

    this.windowresize();

  }


  locateClient = (clientIp) => {
    fetch(`https://ip-geolocation.whoisxmlapi.com/api/v1?apiKey=at_QpFWMwL7iIKJB5ogKNWW069mPbNnW&ipAddress=${clientIp}`).then((data)=>{
      return data.json();
    }).then((locationInfo)=>{
      this.setState({userLat: locationInfo.location.lat, userLng: locationInfo.location.lng, cityName: locationInfo.location.city});
      let countryCode = locationInfo.location.country;
      this.getCountry(countryCode);
      let cityArray = Array.from(locationInfo.location.city);
      let cityNameFiltered = '';
      
      cityArray.forEach((letter)=>{
        cityNameFiltered = cityNameFiltered + letter;
        return cityNameFiltered;
      })
      this.setState({cityNameNoSpace: cityNameFiltered});
      this.getWeatherData(this.state.cityNameNoSpace);

    });
  }

  getCountry = (countryCode) => {
    fetch(`https://restcountries.eu/rest/v2/alpha/${countryCode}`).then((data2)=>{
        return data2.json();
      }).then((data3)=>{
        let countName = data3.name;
        this.setState({country: countName});
      });
  }

  getWeatherData = (userCity) => {
    fetch(`https://api.openweathermap.org/data/2.5/onecall?lat=${this.state.userLat}&lon=${this.state.userLng}&units=metric&appid=7a8a7c8376d6bf111c3dcf7410c26c1e`).then((data)=>{
      return data.json();
    }).then((weatherInfo)=>{
      this.setState({weatherData: weatherInfo, currentDt: weatherInfo.current.dt});
      console.log(this.state.weatherData);
      this.getCurrentDate(this.state.weatherData.current.dt);
    });
  }

  getCurrentDate = (timestamp) => {
    fetch(`https://showcase.api.linx.twenty57.net/UnixTime/fromunixtimestamp?unixtimestamp=${timestamp}`).then(data=>{
        return data.json();
    }).then(data2=>{
      this.setState({currentTime: data2});
    })
  }

  windowresize = () => {
    window.addEventListener("resize",()=>{
      this.setState({width: window.innerWidth});
      console.log(window.innerWidth);
    })
  }

  render() {

    let siteContent = <div></div>;

    if(this.state.currentTime!==undefined) {
      console.log(window.innerWidth);

      if(this.state.width>980){

        siteContent = <div className="whole-container">
        <div className="lside">
          <Mainblock states={this.state}/>
          <Weekly states={this.state}/>
        </div> 
        <Hourly states={this.state}/>
        </div>;
      }
      else {
        siteContent = <div className="whole-container">
          <Mainblock states={this.state}/>
          <Hourly states={this.state}/>
          <Weekly states={this.state}/>
        </div>;
      }

    document.querySelector('.loading-block').classList.add('disappears');
    }


    return (
      <div>
        <div className="loading-block">
          <p className="loading-text">Hold on dude, it's loading</p>
        </div>
        {siteContent}
      </div>
    );
    
  }
}

export default App;
