import React from 'react';
import Weekday from './Weekday';
import '../Styles/Weekly.css';


function Weekly({states}) {

    let weekdays = states.weatherData.daily;
    let currentTime = states.currentTime.Datetime;
    let dayOfMonth = (currentTime[8]*10+currentTime[9]*1);
    console.log(dayOfMonth);
    let month = 'January';
    let nextWeek = Array.from(weekdays).filter((item,index)=>{
        return index > 0;
    });

    let monNum = currentTime[5]*10+currentTime[6]*1;

    function montChoose(monthNumber){
        if(monthNumber>12){
            monthNumber = monthNumber-1;
        }
        switch(monthNumber) {
            case 1:
                month = 'Jan'
                break;
            case 2:
                month = 'Feb'
                break;
            case 3:
                month = 'Mar'
                break;
            case 4:
                month = 'Apr'
                break;
            case 5:
                month = 'May'
                break;
            case 6:
                month = 'Jun'
                break;
            case 7:
                month = 'Jul'
                break;
            case 8:
                month = 'Aug'
                break;
            case 9:
                month = 'Sep'
                break;
            case 10:
                month = 'Oct'
                break;
            case 11:
                month = 'Nov'
                break;
            case 12:
                month = 'Dec'
                break;
            default: month = 'Jan'
        }
        return month;
    }

    montChoose(monNum);

    let maxDays = 31;
    switch(true) {
        case (monNum===1||monNum===3||monNum===5||monNum===7||monNum===8||monNum===10||monNum===12):
            maxDays = 31;
            break;
        case (monNum===4||monNum===6||monNum===9||monNum===11):
            maxDays = 30;
            break;
        case (monNum===2 && Number(`${currentTime[0]}${currentTime[1]}${currentTime[2]}${currentTime[3]}`)%4===0):
            maxDays = 29;
            break;
        case (monNum===2):
            maxDays = 28;
            break;
        default: maxDays = 31;
    }
            

    return (
        <div className="weekly">
            <div className="weekly-forecast">
                <p>Weekly Forecast</p>
            </div>
            <div className="forecasts">
            {nextWeek.map((item,index)=>{
                dayOfMonth = dayOfMonth+1;
                let dayReplacement = 10;
                if(dayOfMonth>maxDays){
                    dayReplacement = dayOfMonth-maxDays;
                    montChoose(monNum+1);
                }
                else {
                    dayReplacement = dayOfMonth;
                }
                let weatherCondition = item.weather[0].id;
                let dayOfWeek = 'Fail';
                let timeDifference = (item.dt - 1578297600)/86400;
                switch(true) {
                    case (timeDifference%7<1):
                        dayOfWeek = 'Mon'
                        break;
                    case (timeDifference%7<2):
                        dayOfWeek = 'Tue'
                        break;
                    case (timeDifference%7<3):
                        dayOfWeek = 'Wed'
                        break;
                    case (timeDifference%7<4):
                        dayOfWeek = 'Thu'
                        break;
                    case (timeDifference%7<5):
                        dayOfWeek = 'Fri'
                        break;
                    case (timeDifference%7<6):
                        dayOfWeek = 'Sat'
                        break;
                    case (timeDifference%7<7):
                        dayOfWeek = 'Sun'
                        break;
                    default: dayOfWeek = 'Mon'
                }
                return <Weekday key={index} day={dayReplacement} month={month} dayOfWeek={dayOfWeek} 
                dayTemp={Math.round(item.feels_like.day)} nightTemp={Math.round(item.feels_like.night)} condition={weatherCondition}/>
            })}
            </div>
        </div>
    );
}



export default Weekly;