import React from 'react';
import '../Styles/Hourly.css';
import Hourcard from './Hourcard';
import Scroll from './Scroll';


function Hourly({states}) {

    let currentTime = states.currentTime.Datetime;
    let hours = states.weatherData.hourly;
    console.log(currentTime);
    let ch = Number(currentTime[11]+currentTime[12])+4;
    let cardList = Array.from(hours).filter((item,index)=>{
        return index>0 && index<13
    })

    
    return (
        <div className="hourly">
            <div className="hourly-forecast">
                <p>Hourly Forecast</p>
            </div>
            <Scroll >
                {cardList.map((item,index)=>{
                    if(ch>22){
                        ch=ch-24;
                    }
                    ch=ch+1;
                    let ch_placement = '';
                    if(ch<10){
                        ch_placement = `0${ch}`
                    }
                    else {
                        ch_placement = `${ch}`
                    }
                    return <Hourcard key={index} hour={ch_placement} temp={Math.round(item.temp)} description={item.weather[0].main} condition={item.weather[0].id}/>
                })}
            </Scroll>
        </div>
    );
}



export default Hourly;